.account-section form .form-label {
  font-weight: 500;
}
.account-section form .form-control:focus,
.account-section form .form-select:focus {
  border-color: var(--bs-primary);
  box-shadow: 0 0 0 0.2rem rgba(var(--bs-primary-rgb), 0.25);
}

#mobile-nav-select {
  font-weight: 500;
}

.card {
  transition: box-shadow 0.2s ease-in-out;
}
.card:hover {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}

.btn-outline-secondary.text-uppercase {
  font-weight: 600;
  letter-spacing: 0.5px;
  padding: 0.375rem 1rem;
}

#loading-section {
  animation: fadeIn 0.3s ease-in-out;
}

.account-section {
  animation: fadeIn 0.3s ease-in-out;
}
.account-section.d-none {
  display: none !important;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#delete-section .card.border-danger {
  border-width: 2px;
}
#delete-section #delete-account-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
#delete-section .form-check-label {
  user-select: none;
}